import React from "react";

export const FloatingBox = (props) => {
    return (<div className="l-full-row">
                <div className="l-page-row">
                    <div className="u-padding-top-s u-padding-s">
                        <div className={`g-white1-bg u-box-shadow ${props.boxClassName}`}>
                            {props.children}
                        </div>
                    </div>
                </div>
            </div>)
};

export const LabelCell = (props) => {
    return (<td className="u-padding-top-l u-padding-l u-padding-right-l">{props.label}</td>)
};

export const ButtonCell = (props) => {
    return (
        <td className="opl-checkout__button__cell u-padding-top-l u-padding-l">
            <a href={"#"} className="opl-product-addlink u-right u-small-right"
               onClick={props.onclick}>

            </a>
        </td>
    )
};

export const PriceTiers = (props) => {
    let [head, ...tail] = props.prices;
    return (<div>
            <p>
                <span>{head.integer},{head.fraction} {head.currency}/mc </span>
                {head.end ? <span>{head.start}-{head.end} mc</span> : <span>od {head.start} mc</span>}
            </p>
            {tail && tail.length>0 && <PriceTiers prices={tail}/>}</div>
    );
};



